import React, { Component, Fragment } from 'react';
import Contacts from '../Contacts/Contacts';

import styles from './Modal.module.css';

export default class Modal extends Component {
    state = {
        showModal: false,
    };

    componentDidMount() {
        window.addEventListener('keydown', this.handleKeyDown);
    }

    componentWillUnmount() {
        window.removeEventListener('keydown', this.handleKeyDown);
    }

    toggleModal = () => {
        this.setState(prevState => ({
            showModal: !prevState.showModal,
        }));
    };

    handleKeyDown = event => {
        if (event.code === 'Escape') {
            this.toggleModal();
        }
    };

    handleCloseByBackdrop = () => {
        this.toggleModal();
    };

    render() {
        let { showModal } = this.state;
        return (
            <Fragment>
                {!showModal && (
                    <button
                        className={styles.contactsBtn}
                        onClick={this.toggleModal}
                        type="button"
                    >
                        Contacts
                    </button>
                )}
                {showModal && (
                    <div
                        onClick={this.handleCloseByBackdrop}
                        className={styles.Backdrop}
                    >
                        <div className={styles.Modal}>
                            <Contacts />
                            <button className={styles.closeBtn} type="button">
                                <svg
                                    className={styles.closeSvg}
                                    version="1.1"
                                    id="Layer_1"
                                    x="0px"
                                    y="0px"
                                    viewBox="0 0 96 96"
                                    enableBackground="new 0 0 96 96"
                                >
                                    <polygon
                                        fill="#AAAAAB"
                                        points="96,14 82,0 48,34 14,0 0,14 34,48 0,82 14,96 48,62 82,96 96,82 62,48 "
                                    />
                                </svg>
                            </button>
                        </div>
                    </div>
                )}
            </Fragment>
        );
    }
}
