import React, { Component, Fragment } from 'react';
import items from './utils/tabs.json';

import styles from './Tabs.module.css';

export default class Tabs extends Component {
    state = {
        activeIndex: 0,
        subscription: '',
        checked: true,
    };

    activeTabIndex = (event, idx) => {
        this.setState({
            subscription: event.target.value,
            activeIndex: idx,
            checked: '',
        });
    };

    render() {
        let tab = items[this.state.activeIndex];

        return (
            <Fragment>
                <form className={styles.wrapTabs}>
                    {items.map((item, idx) => (
                        <Fragment key={item.label}>
                            <input
                                id={item.label}
                                className={styles.radioBtn}
                                type="radio"
                                name={item.label}
                                value={item.label}
                                onChange={e => this.activeTabIndex(e, idx)}
                                checked={
                                    item.checked === this.state.checked ||
                                    item.label === this.state.subscription
                                }
                            />
                            <label
                                htmlFor={item.label}
                                className={styles.label}
                            >
                                <span className={styles.btn}>{item.label}</span>
                            </label>
                        </Fragment>
                    ))}
                </form>
                <ul className={styles.tabContent}>
                    {tab.data.map(task => (
                        <li key={task.description} className={styles.tab}>
                            <a
                                className={styles.tabContentLink}
                                href={task.content}
                                target="_blank"
                                rel="noreferrer"
                            >
                                {task.description}
                            </a>
                        </li>
                    ))}
                </ul>
            </Fragment>
        );
    }
}
