import React from 'react';
import items from './utils/Education.json';

import styles from './Education.module.css';

export default function Education() {
    return (
        <ul className={styles.educationContainer}>
            {items.map(item => {
                return (
                    <li key={item.label}>
                        <div className={styles.educationList}>
                            <p className={styles.itemText}>{item.label}</p>
                            <p className={styles.itemText}>{item.period}</p>
                            <a
                                target="_blank"
                                rel="noreferrer"
                                className={styles.itemLink}
                                href={item.content}
                            >
                                {item.type}
                            </a>
                        </div>
                    </li>
                );
            })}
        </ul>
    );
}
