import React, { Fragment } from 'react';

export default function Birth() {
    let birth = new Date(1982, 3 - 1, 20);
    let year = birth.getFullYear();
    let today = new Date();

    let result =
        today.getFullYear() -
        year -
        (today.getTime() < birth.setFullYear(year));

    return <Fragment>{result}</Fragment>;
}
